#cardbody {
  border: solid black 2px;
  box-shadow: 10px 10px 2px lightgray;
}

#defaultValue {
  font-size: 0.1em;
}
#option1 {
  background-color: #ffcc66;
}
#box {
  color: black;
}
#checked {
  width: 30px;
}

.slider {
  -webkit-appearance: none;
  width: 100%;
  height: 5px;
  border-radius: 5px;
  background: #d3d3d3;
  outline: none;
  opacity: 0.7;
  -webkit-transition: 0.2s;
  transition: opacity 0.2s;
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background: rgb(248, 204, 118);
  cursor: pointer;
}

.slider::-moz-range-thumb {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background: rgb(248, 204, 118);
  cursor: pointer;
}
