#post {
  border: none;
  background-color: lightseagreen;
}
.slider {
  -webkit-appearance: none;
  width: 100%;
  height: 5px;
  border-radius: 5px;
  background: #d3d3d3;
  outline: none;
  opacity: 0.7;
  -webkit-transition: 0.2s;
  transition: opacity 0.2s;
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background: rgb(248, 204, 118);
  cursor: pointer;
}

.slider::-moz-range-thumb {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background: rgb(248, 204, 118);
  cursor: pointer;
}

.button {
  background-color: #ffcc66 !important;
}

.button:hover {
  background: rgb(207, 150, 35) !important;
  transition: 1s;
}

.btn-secondary.active {
  background: #ffcc66 !important;
  color: black !important;
  transition: 0.5s;
}
